.inputBlock {
  display: flex;
  flex-direction: column;
  row-gap: 6px;
}

.undertext {
  font-size: 16px;
  font-weight: 600;
}

.input {
  padding: 5px 10px;
  max-width: 300px;

  background: #bbe3f6;
  border-radius: 10px;

  font-size: 16px;
  
  &::placeholder {
    color: #63b8df;
  }
}
